/**
 *  ANode props  name 
 *
 * @param {Object} aNode ANode
 * @param {string} name name
 * @return {Object}
 */
function getANodeProp(aNode, name) {
    var index = aNode.hotspot.props[name];
    if (index != null) {
        return aNode.props[index];
    }
}

exports = module.exports = getANodeProp;
