var ExprType = require('./expr-type');

/**
 * 
 *
 * @param {Array} paths 
 * @return {Object}
 */
function createAccessor(paths) {
    return {
        type: ExprType.ACCESSOR,
        paths: paths
    };
}

exports = module.exports = createAccessor;
