var evalExpr = require('../runtime/eval-expr');

/**
 *  s-bind 
 *
 * @param {Object} sBind bind
 * @param {Model} scope 
 * @param {Component} owner 
 * @return {boolean}
 */
function nodeSBindInit(sBind, scope, owner) {
    if (sBind && scope) {
        return evalExpr(sBind.value, scope, owner);
    }
}

exports = module.exports = nodeSBindInit;
