var evalExpr = require('./eval-expr');

/**
 * 
 *
 * @param {Array} args 
 * @param {Data} data 
 * @param {Component} owner 
 * @return {Array}
 */
function evalArgs(args, data, owner) {
    var result = [];
    for (var i = 0; i < args.length; i++) {
        result.push(evalExpr(args[i], data, owner));
    }

    return result;
}

exports = module.exports = evalArgs;
