// #[begin] reverse
/**
 * 
 *
 * @param {HTMLElement|HTMLComment} target 
 * @param {string} type 
 * @return {boolean}
 */
function isEndStump(target, type) {
    return target.nodeType === 8 && target.data === '/s-' + type;
}
// #[end]

exports = module.exports = isEndStump;
