/**
 *  kebab case  camel case
 *
 * @param {string} source 
 * @return {string}
 */
function kebab2camel(source) {
    return source.replace(/-+(.)/ig, function (match, alpha) {
        return alpha.toUpperCase();
    });
}

exports = module.exports = kebab2camel;
