/**
 * ident
 *  ident (identifier)
 * ($)(_)
 *
 * @inner
 * @param {Walker} walker 
 * @return {string}
 */
function readIdent(walker) {
    var match = walker.match(/\s*([\$0-9a-z_]+)/ig, 1);

    // #[begin] error
    if (!match) {
        throw new Error('[SAN FATAL] expect an ident: ' + walker.cut(walker.index));
    }
    // #[end]

    return match[1];
}

exports = module.exports = readIdent;
